import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import VueWechatTitle from 'vue-wechat-title'
import Login from '@/pages/Login'
import Video from '@/pages/Video'
import Index from '@/pages/Index'
import Rong from '@/pages/Rong'
Vue.use(Router)
Vue.use(VueWechatTitle)
Vue.use(Login)
Vue.use(Video)
Vue.use(Index)

export default new Router({
  routes: [
	{
      path: '/',
      name: 'Video',
      component: Video,
      meta: {
      	title :'wdj->bitch'
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
      	title :'登录'
   	  }
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      meta: {
        title :'主页',
        // 添加该字段，表示进入这个路由是需要登录的
        requireAuth: true
      }
    },
    {
      path: '/rong',
      name: 'Rong',
      component: Rong,
      meta: {
        title :'蓉蓉',
        // 添加该字段，表示进入这个路由是需要登录的
        requireAuth: true
      }
    }

  ]
})



