<template>
	<div class="body" >

		<img src="../assets/sevenyear.png" alt="">
        <div sytle="text-align:center;" >
         <el-button type="primary" @click="enterRong()">咱家蓉蓉的小屋</el-button>
            
        </div>
	</div>

</template>

<script>
    export default {
    data() {
      return {
        
           
      }
    },
    methods: {
            enterRong(){
                var token = this.$store.state.token;
                console.log("token"+token)
               this.$axios("/rong",{
                  headers:{
                    'Authorization': token
                  }
                }).then(res =>{
                    console.log(res.data)
                    if(res.data==1){
                        this.$router.push({ path: 'rong' }) 
                    }else{
                        this.$message.error('蓉蓉只属于那个人！');
                    }
                }).catch(res =>{
                    this.$message.error('蓉蓉只属于那个人！');
                })
            }
    }
  }
</script>

<style>
	.body {
  width: 50%; 
  height: 80%; 
  overflow: auto; 
  margin: auto; 
  position: absolute; 
  top: 0; left: 0; bottom: 0; right: 0; 
	}
	.shadow {
    display:inline-block;
   /*  height: 500px; */
    -webkit-border-radius: 8px;

    -moz-border-radius: 8px;

    border-radius: 8px;

    -webkit-box-shadow: #666 0px 0px 10px;

    -moz-box-shadow: #666 0px 0px 10px;

    box-shadow: #666 0px 0px 10px;behavior: url(/PIE.htc);
}

</style>


