<template>
	<div class="app-head">
		<div class="app-head-inner">
      <router-link to="/">
        <img src="../assets/logo.png">
      </router-link>
			<span>pinpinduo.com</span>
			<div class="head-nav">
			<ul>
        <router-link to="/login" class="sign">
				<li>登录</li>
         </router-link>
				<li class="nav-pile">|</li>
        
         <li>注册</li>
       
				<li class="nav-pile">|</li>
				<li>关于</li>
			</ul>
			</div>
		</div>
	</div>
</template>

<script>
export default{
	name:"headers",
	data(){
		return{
			
		}
	}
}
</script>

<style>
	
.app-head {
  background: #363636;
  color: #b2b2b2;
  width: 100%;
}
.app-head-inner {
  width: 1200px;
  margin: 0 auto;
}
.head-logo {
  float: left;
}
.app-head-inner span{
  position: relative;
  top: -20px;
}
.app-head-inner img {
  width: 50px;
  margin-top: 0px;
}
.head-nav {
  float: right;
}
.head-nav ul {
  overflow: hidden;
  list-style:none;
}
.head-nav li {
  cursor: pointer;
  float: left;
}
.nav-pile {
  padding: 0 10px;
}
.container {
  width: 1200px;
  margin: 0 auto;
}
.hr {
  height: 1px;
  width: 100%;
  background: #ddd;
}
.button {
  background: #4fc08d;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
}
.button:hover {
  background: #4fc08d;
}
.sign {
  color:#b2b2b2;
}


</style>


