<template>
    <el-form class="form demo-ruleForm" ref="form" :model="form" :rules="rules" label-position="left" label-width="0px" >
        <h3 style="height:50px;line-height:50px; margin:0px;">登录</h3>
        <el-form-item prop="username">
            <el-input type="text" v-model="form.username"  auto-complete="off" placeholder="请输入账号" style="margin:10px auto;" ></el-input>
        </el-form-item>
        <el-form-item prop="password">
           <el-input v-model="form.password" auto-complete="off" placeholder="请输入密码" style="margin:10px auto;"></el-input>
        </el-form-item>
        
        <el-form-item style="width:100%;text-align:center;">
            <el-button type="primary" @click="submitForm('form')">登录</el-button>
        </el-form-item>
    </el-form>
</template>

<script>  export default {
    data() {
      return {
        form: {
          username: '',
          password: ''
        },
        rules: {
          username:[
            {required: true, message: '请输入用户名', trigger: 'blur'},
            { min: 5, max: 25, message: '长度在 5 到 25 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      async submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios("/login",{
              params:{
                 username:this.form.username,
                 password:this.form.password   
             }
            })
            .then(res => {
              var token = res.data;
              console.log(res.data);
               if(token!=null){ 
                this.$router.push({ path: 'index' })
                console.log(token.token) 
                this.$store.state.token=token.token;
              
                }else{
         
                  this.$message.error('请输入正确的用户名和密码！！！');
                }

            })
            .catch(rep => {
              console.log(rep)
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
        console.log("再次请求")
      
        
      },
    redirectRong(){

    }
    }
  }
 </script>
<style scoped>
    .form {
       -webkit-border-radius: 5px;
      border-radius: 5px;
      -moz-border-radius: 5px;
      background-clip: padding-box;
      margin: 180px auto;
      width: 350px;
      padding: 35px 35px 15px 35px;
      background: #fff;
      border: 1px solid #eaeaea;
      box-shadow: 0 0 25px #cac6c6;
    }
  
</style>


